.arrow {
  background-color: transparent;
  border: none;
  outline: none;
}

.arrow-image {
  position: absolute;
  left: 92.78%;
  right: 3.08%;
  top: 86.56%;
  bottom: 6.82%;

  transition: -webkit-filter 1s ease;

  transition: filter 1s ease;

  transition: filter 1s ease, -webkit-filter 1s ease;
}

.arrow-image-light:hover {
  -webkit-filter: invert(25%);
          filter: invert(25%);
}

.arrow-image-dark {
  transition: -webkit-filter 1s ease;
  transition: filter 1s ease;
  transition: filter 1s ease, -webkit-filter 1s ease;
  -webkit-filter: invert(100%);
          filter: invert(100%);
}

.arrow-image-dark:hover {
  -webkit-filter: invert(75%);
          filter: invert(75%);
}

.autoarrow {
  background-color: transparent;
  border: white;
  outline: white;
}

.autoarrow-image {
  position: absolute;
  left: 3.08%;
  right: 92.78%;
  top: 86.56%;
  bottom: 6.82%;
  width: 5%;

  transition: -webkit-filter 1s ease;

  transition: filter 1s ease;

  transition: filter 1s ease, -webkit-filter 1s ease;
}

